import React, { useEffect } from 'react'
import { connect } from 'react-redux'
import {
    followUser,
    getUsersFromServer,
    setCurrentPage,
    setCurrentPortion,
    unfollowUser
} from '../../Redux/usersReducer'
import Users from './Users'
import Preloader from '../Common/Preloader/Preloader'
import { compose } from 'redux'
import {
    getCurrentPage, getCurrentPortion,
    getFollowingInProgress, getIsFetching,
    getPageSize, getPortionSize, getTotalPages, getTotalPortions,
    getUsersFromState
} from '../../Redux/UsersSelectors'

const UsersContainer = (props) => {
    useEffect(() => {
        props.getUsersFromServer(props.currentPage, props.pageSize)
    }, [props.currentPage])

    const onPageChanged = (page,portion) => {
        props.setCurrentPage(page)
        props.setCurrentPortion(portion)
    }

    return <div>
        {props.isFetching ? <Preloader/> : null}
        <Users
            currentPortion={props.currentPortion}
            portionSize={props.portionSize}
            portions={props.portions}
            currentPage={props.currentPage}
            onPageChanged={onPageChanged}
            users={props.users}
            followingInProgress={props.followingInProgress}
            followUser={props.followUser}
            unfollowUser={props.unfollowUser}
            pages={props.pages}
        />
    </div>
}

const mapStateToProps = (state) => {
        return {
            currentPortion: getCurrentPortion(state),
            portionSize: getPortionSize(state),
            portions: getTotalPortions(state),
            users: getUsersFromState(state),
            pageSize: getPageSize(state),
            currentPage: getCurrentPage(state),
            followingInProgress: getFollowingInProgress(state),
            pages: getTotalPages(state),
            isFetching:getIsFetching(state)
        }
    },
    ACObject = {setCurrentPage, getUsersFromServer, followUser, unfollowUser,setCurrentPortion}

export default compose(
    connect(mapStateToProps, ACObject)
)(UsersContainer)