import { createSelector } from 'reselect'

export const getUsersFromState = (state) => {
        return state.usersPage.users
    },
    getPageSize = (state) => {
        return state.usersPage.pageSize
    },
    getTotalUsersCount = (state) => {
        return state.usersPage.totalCount
    },
    getCurrentPage = (state) => {
        return state.usersPage.currentPage
    },
    getFollowingInProgress = (state) => {
        return state.usersPage.followingInProgress
    },
    getTotalPages = createSelector(getTotalUsersCount, getPageSize,
        (totalCount, pageSize) => {
            let pagesCount = Math.ceil(totalCount / pageSize),
                pages = []
            for (let i = 1; i <= pagesCount; i++) {
                pages.push(i)
            }
            return pages
        }),
    getPortionSize = (state) => {
        return state.usersPage.portionSize
    },
    getCurrentPortion = (state) => {
        return state.usersPage.currentPortion
    },
    getTotalPortions = createSelector(getTotalPages, getPortionSize,
        (pagesCount, portionSize) => {
            return Math.ceil(pagesCount.length / portionSize)
        }),
    getIsFetching = (state) => {
        return state.usersPage.isFetching
    }
